.BlockRaw__Editor {
      border-radius: 0.25rem;
      border-width: 1px;
      --tw-border-opacity: 1;
      border-color: rgb(167 167 167 / var(--tw-border-opacity))
}
  .BlockRaw__Modal__Content {
      display: flex;
      flex-direction: column;
      padding: 1rem
}
  .BlockRaw__Modal__Content__Close {
      align-self: flex-end
}
  .BlockRaw__Modal__Content__Close__Icon {
      font-size: 1.25rem;
      line-height: 1.75rem
}
  .BlockRaw__Modal__Content__Close__Icon:hover {
      --tw-text-opacity: 1;
      color: rgb(220 48 24 / var(--tw-text-opacity))
}
  @media (min-width: 768px) {
      .BlockRaw__Modal__Content__Close__Icon {
            font-size: 1.875rem;
            line-height: 2.25rem
      }
}
  .BlockRaw__Modal__Content__Image {
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 1.25rem
}
  .BlockRaw__Modal__Content__Infos {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      --tw-text-opacity: 1;
      color: rgb(51 51 51 / var(--tw-text-opacity))
}
  @media (min-width: 1024px) {
      .BlockRaw__Modal__Content__Infos {
            padding-left: 3rem;
            padding-right: 3rem;
            padding-bottom: 3rem
      }
}
  .BlockRaw__Modal__Content__Description {
      margin-bottom: 1rem;
      text-align: center
}
  .BlockRaw__Modal__Content__Actions {
      margin-bottom: 1.5rem;
      display: flex;
      align-items: center;
      gap: 0.5rem
}
  .BlockRaw__Modal__Content__CBLabel {
      margin-bottom: 0px;
      user-select: none;
      letter-spacing: 0em;
      line-height: inherit
}
  .BlockRaw__Modal__Content__Accept {
      border-radius: 0.375rem;
      --tw-bg-opacity: 1;
      background-color: rgb(220 48 24 / var(--tw-bg-opacity));
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      padding-left: 1rem;
      padding-right: 1rem;
      --tw-text-opacity: 1;
      color: rgb(255 255 255 / var(--tw-text-opacity))
}
  .BlockRaw__Modal__Content__Accept:hover {
      --tw-bg-opacity: 1;
      background-color: rgb(250 83 60 / var(--tw-bg-opacity))
}