.BlockButton {

    display: flex;

    flex-direction: column;

    gap: 1rem
}

  .BlockButton__Config {

    display: flex;

    flex-direction: row;

    align-items: flex-end;

    gap: 1rem
}

  .BlockButton__Config >:first-child {

    width: 50%
}

  @media (min-width: 768px) {

    .BlockButton__Config >:first-child {

        width: 66.666667%
    }
}

  .BlockButton__Config >:nth-child(2) {

    width: 50%
}

  @media (min-width: 768px) {

    .BlockButton__Config >:nth-child(2) {

        width: 33.333333%
    }
}