.BlockSeparator__Config {
  
    margin-bottom: 1.5rem;
  
    display: flex;
  
    flex-direction: row;
  
    align-items: flex-end;
  
    gap: 1rem
}
  
    .BlockSeparator__Config >:first-child {
  
    width: 50%
}
  
    .BlockSeparator__Config >:nth-child(2) {
  
    width: 50%
}

  .BlockSeparator__Preview {
  
    border-radius: 0.375rem;
  
    border-width: 1px;
  
    border-style: dotted;
  
    --tw-border-opacity: 1;
  
    border-color: rgb(0 0 0 / var(--tw-border-opacity));
  
    padding: 1rem
}

  .BlockSeparator__Preview--element {
  
    height: 1px;
  
    --tw-bg-opacity: 1;
  
    background-color: rgb(0 0 0 / var(--tw-bg-opacity))
}