.BlockTitle {

    display: flex;

    flex-direction: column;

    gap: 1rem
}

@media (min-width: 768px) {

    .BlockTitle {

        flex-direction: row;

        align-items: flex-end
    }
}

  .BlockTitle >:first-child {

    width: 41.666667%
}

  .BlockTitle >:nth-child(2) {

    width: 58.333333%
}